
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

 
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
import ErrorComponent from '../components/Error';
import RefreshComponent from '../components/Refresh';
import LoaderComponent from '../components/Loader';

class Metrics extends React.Component {
  constructor(props){
    super(props);
  }
 
  render(){
 
    if (this.props.error !== ''){
      return (
        <div class="container">
            <ul class = "pagination justify-content-center">
              <RefreshComponent call = {this.props.setMetrics}/>
            </ul>
              <ErrorComponent error={this.props.error_code}/>
        </div>
      );
    }
    else if (this.props.loader){
        return (
          <div>
            <LoaderComponent />
          </div>
        )
    } 
    else {
      return (
      <div class="card">
      <div class="card-header">
        <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify2" role = "button" 
           aria-expanded = "true" aria-controls = "streamnotify" id = "statslinknotify">Metrics
        </a>
        <ul class = "pagination float-right">
          <RefreshComponent call = {this.props.setMetrics}/>  
        </ul> 
      </div>
      <div className = "collapse" id = "streamnotify2">
      <div class= "card-body">
      <ul class="list-group">
          <li class="list-group-item d-flex justify-content-between align-items-center">
              <b>Active Jobs</b>
            <span  class="items1s" class="badge badge-secondary badge-pill">{this.props.metrics.active_jobs}</span>
          </li>
          <li class="list-group-item d-flex justify-content-between align-items-center">
              <b>Active Subscribers</b>
            <span class="badge badge-secondary badge-pill">{this.props.metrics.active_subscribers}</span>
          </li>
          <li class="list-group-item d-flex justify-content-between align-items-center">
              <b>Etl Jobs</b>
            <span class="badge badge-secondary badge-pill">{this.props.metrics.etl_jobs}</span>
          </li>
          <li class="list-group-item d-flex justify-content-between align-items-center">
              <b>Cron Jobs</b>
            <span class="badge badge-secondary badge-pill">{this.props.metrics.cron_jobs}</span>
          </li> 
          <li class="list-group-item d-flex justify-content-between align-items-center">
              <b>Build Time</b>
            <span class="badge badge-secondary badge-pill">{this.props.metrics.build_time}</span>
          </li>  
      </ul>
      </div>
      </div>
  </div>
    );}
  }
}

 
export default Metrics;
 

