
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
 
//Bootstrap and jQuery libraries
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

import MemoryGraph from '../cards/MemoryGraph';
import Metrics from '../cards/Metrics';
import BarGraph from '../cards/BarGraph';
import { fetchCall } from '../utils/GraphQl';

class Stats extends React.Component {
  constructor(props){
    super(props);
    this.state  = { 
      metrics: [],
      error: '',
      loader: false,
      error_code: 0
    }
  }

  async fetchEtlFlowMetrics() {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
 
    this.setState(prevState => ({
      loader: true,
      error: ''
    }))
    
    var body = ` query { metrics { active_jobs  active_subscribers etl_jobs cron_jobs  build_time } }`
    
    const response = await fetchCall(GRAPHQL_URL,body)  
 
    if (response.status === 403) {
      this.props.logout() 
      localStorage.removeItem("Authorization")
    } 

    if (!response.ok) {
      var message = `An error has occured: ${response.statusText}`;
      this.setState(prevState => ({
        error_code: response.status
      }))
      throw  new Error(message);
    } 
    else {  
      const {data} = await response.json();
      return data; 
    }  
  }
 
  componentDidMount() {
    this.setMetrics()
  }
 
  setMetrics = () => {
    this.fetchEtlFlowMetrics().then(({metrics}) => {
      this.setState(prevState => ({
          metrics: metrics,
          loader: false
      }))
    }).catch(error => {
      this.setState(prevState => ({
        error: error,
        loader: false
      }))
  });  
  }
  
  render(){
    return (
      <div class = "container-fluid">
                <MemoryGraph GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.logout}  />    
         <br/>
        <div class = "row">
            <div class="col-sm-9">
                <BarGraph GRAPHQL_URL = {this.props.GRAPHQL_URL}  logout={this.props.logout} />   
            </div>
            <div class="col-sm-3">
                <Metrics  metrics = {this.state.metrics} setMetrics={this.setMetrics} error = {this.state.error} error_code = {this.state.error_code} loader = {this.state.loader}/>  
            </div>    
        </div>
      </div>
  );}
  }       
 
export default Stats;
 

