import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

import '../css/App.css'

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import LoaderComponent from '../components/Loader';
import ErrorComponent from '../components/Error';
import RefreshComponent from '../components/Refresh';
import JobListData from '../cards/JobList';

class JobList extends React.Component {
  constructor(props){
    super(props);
  }

  render(){  
    if (this.props.error !== ''){
      return (
        <div class="container">
          <ul class = "pagination justify-content-center">
          <RefreshComponent call = {this.props.get_etl_jobs}/>
          </ul>
          <ErrorComponent error={this.props.error_code}/>
        </div>
      );
     } 
     else if (this.props.loader){
      return (
        <div>
          <LoaderComponent />
        </div>
      );
    }
    else{
    return (
        <div class="container-fluid">
          <JobListData  data = {this.props.jobs} get_etl_jobs = {this.props.get_etl_jobs} GRAPHQL_URL = {this.props.GRAPHQL_URL} />
        </div>     
    );}
  }
}
export default JobList;
