import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
import LoaderComponent from '../components/Loader';
import ErrorComponent from '../components/Error';
import CredentialData from '../cards/Credential';
import RefreshComponent from '../components/Refresh';

import { fetchCall } from '../utils/GraphQl';

class Credential extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { 
      credential: [],
      loader:false,
      error:'',
      error_code:0,

    }
  }
  
  async fetchEtlFlowCreds() {
    
    this.setState(prevState => ({
      loader: true,
      error: ''
    }))

    var body = ` query { credential { name  type  valid_from } } `
    const response = await fetchCall(this.props.GRAPHQL_URL,body)

    if (response.status === 403) {
      this.props.logout() 
      localStorage.removeItem("Authorization")
    } 
    
    if (!response.ok) {
      var message = `An error has occured: ${response.statusText}`;
      this.setState(prevState => ({
        error_code: response.status
      }))
      throw  new Error(message);
    } 
    else {  
      const {data} = await response.json();
      return data;   
  }
}

  componentDidMount() {
    //calling getInfo api
    this.setCredentials()
  }

  setCredentials = () => {
    this.fetchEtlFlowCreds().then(({credential}) => {
      this.setState(prevState => ({
        credential: credential,
        loader: false
      }))
    }).catch(error => {
        this.setState(prevState => ({
          error: error,
          loader: false
        }))
    });
  }

  render(){

    if (this.state.error !== ''){
      return (
        <div class="container">
           <ul class = "pagination justify-content-center">
              <RefreshComponent call = {this.setCredentials}/>
           </ul>
           <ErrorComponent   error ={this.state.error_code}/>
        </div>
      );
    } 
    else if (this.state.loader){
      return (
        <div>
          <LoaderComponent/>
        </div>
      );
    }
    else{
      return (
        <div className="container">
          <CredentialData data = {this.state.credential} setCredentials={this.setCredentials} GRAPHQL_URL = {this.props.GRAPHQL_URL} />
        </div>
      );
   }      
  }
}

export default Credential;