import React from 'react';
//Bootstrap and jQuery libraries
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import '../css/App.css'
import { withRouter } from 'react-router'
import ErrorComponent from '../components/Error';
import LoaderComponent from '../components/Loader';
import { fetchCall } from '../utils/GraphQl';
import RefreshComponent from '../components/Refresh';
import JobRunsData from '../cards/JobRuns';

class JobRuns extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { 
      jobruns: [],
      job_name: '',
      filter: '',
      filter_start_date: '',
      filter_end_date: '',
      error:'',
      error_code:0,
      paginationValue:0
    }
    this.incPaginationValue = this.incPaginationValue.bind(this);
    this.decPaginationValue = this.decPaginationValue.bind(this);
    this.setJobName = this.setJobName.bind(this);
    this.setFilterStartDate = this.setFilterStartDate.bind(this);
    this.setFilterEndDate = this.setFilterEndDate.bind(this);
    this.setInitialState = this.setInitialState.bind(this);
    this.setFilterName = this.setFilterName.bind(this);
  }

  async fetchEtlFlowJobs(value) {
    const GRAPHQL_URL = this.props.GRAPHQL_URL

    const limit = 10;
    const offset = value

    var query = ` query { jobruns(limit: ${limit} , offset: ${offset}){ job_run_id job_name properties state start_time elapsed_time job_type is_master}}`
    
    if(this.props.match.params.job_name !== undefined){
      query = ` query { jobruns(limit: ${limit} , offset: ${offset} ,filter: "IN", jobName: \"${this.props.match.params.job_name}\"  )
              { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`

      if (this.state.filter_end_date !== "") {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\",filter: "IN", jobName: \"${this.props.match.params.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } 

      this.setState(prevState => ({
        filter: 'IN',
        job_name: this.props.match.params.job_name
      }))

    } else {
      if (this.state.filter_end_date !== "" && this.state.filter !== "" ) {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\",filter: \"${this.state.filter}\", jobName: \"${this.state.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else if (this.state.filter_end_date !== "" ){
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\")
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else if (this.state.filter !== "" && this.state.job_name !== "") {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset}, filter: \"${this.state.filter}\", jobName: \"${this.state.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else if (this.state.filter === "" && this.state.job_name !== "" ) {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset} ,filter: "IN", jobName: \"${this.state.job_name}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      } else {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset}){ job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
      }
    }

    this.setState(prevState => ({
      loader: true,
      error: ''
    }))

    const response = await fetchCall(GRAPHQL_URL,query)

    if (response.status === 403) {
      this.props.logout() 
      localStorage.removeItem("Authorization")
    } 

    if (!response.ok) {
      var message = `An error has occured: ${response.statusText}`;
      this.setState(prevState => ({
        error_code: response.status
      }))
      throw  new Error(message);
    } 
    else {  
      this.setState(prevState => ({
        loader: false
      }))
      const {data} = await response.json();
      return data;  
    } 
  }

  setJobName(value) {
  this.setState(prevState => ({
    job_name: value
  }))
  }

  setFilterName(value) {
    this.setState(prevState => ({
      filter: value
    }))
    }

  setFilterStartDate(value) {
   this.setState(prevState => ({
    filter_start_date: value
   }))
  }

  setFilterEndDate(value) {
   this.setState(prevState => ({
    filter_end_date: value
   }))
  } 

  componentDidMount() {
    this.setJobRuns()
  }

  componentWillUnmount() {
   clearInterval(this.timerID);
  }

  setJobRuns = value => {
    const offset = value || this.state.paginationValue;
    this.fetchEtlFlowJobs(offset).then(({jobruns}) => {
      this.setState(prevState => ({
        jobruns: jobruns,
        loader: false
      }))
     }).catch(error => {
      this.setState(prevState => ({
        error: error,
        loader: false
      }))})
  }
  
  
  incPaginationValue() {
    this.setState(
      prevState => ({ paginationValue: prevState.paginationValue + 10 })
    )
    this.setJobRuns(this.state.paginationValue + 10)
  }

  decPaginationValue() {
    this.setState(
      prevState => ({ paginationValue: prevState.paginationValue - 10 }),
      () =>  this.setJobRuns(this.state.paginationValue)
    )
   
  }

  setInitialState(){
    this.props.match.params.job_name = undefined
    this.setState(prevState => ({  
      job_name: '',
      filter: '',
      filter_start_date: '',
      filter_end_date: '',
      paginationValue: 0
    }),()=> this.setJobRuns(0))
  }

  render(){
    console.log(this.state.paginationValue)
  if (this.state.error !== ''){
    return (
      <div class="container">
          <ul class = "pagination justify-content-center">
            <RefreshComponent call = {this.setJobRuns}/>
          </ul> 
            <ErrorComponent error = {this.state.error_code}/>
      </div>
    );
  }
  else if (this.state.loader){
    return (
      <div>
          <LoaderComponent />
      </div>
    );
  }
  else{
    return (
      <div className="container-fluid">
         <JobRunsData 
           GRAPHQL_URL = {this.props.GRAPHQL_URL}
           data = {this.state.jobruns} 
           jobs = {this.props.jobs}
           setJobRuns= {this.setJobRuns}
           setJobName  = {this.setJobName}
           setFilterStartDate  = {this.setFilterStartDate}
           setFilterEndDate  = {this.setFilterEndDate}
           setFilterName  = {this.setFilterName}
           incPaginationValue = {this.incPaginationValue}
           decPaginationValue = {this.decPaginationValue}
           setInitialState = {this.setInitialState}
           job_name = {this.state.job_name}
           filter = {this.state.filter}
           filter_start_date = {this.state.filter_start_date}
           filter_end_date = {this.state.filter_end_date}
           paginationValue   = {this.state.paginationValue}
         />
      </div>);
   }
  }
}

// export default JobRuns; 
export default withRouter(JobRuns)
