import React from 'react';
//Bootstrap and jQuery libraries
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import '../css/App.css'
import { withRouter } from 'react-router'
import Notification from '../components/Notification';
import { fetchCall } from '../utils/GraphQl';
import RefreshComponent from '../components/Refresh';

class JobRunsData extends React.Component {
  constructor(props){
    super(props); 
    this.job_run_id = ''
    this.filterOperation = ["IN","NOT IN"]
    this.state  = { 
      jobruns: [],
      job_name: '',
      retry_job_name: '',
      filter_start_date: '',
      filter_end_date: '',
      paginationValue: 0,
      loader:false,
      errors: [],
      is_error: false,
      notification: false,
      run_job_props: '',
      error:'',
      error_code:0,

    }

  }


  async runEtlJob(job_name,props) {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
    const token = localStorage.getItem('Authorization')

    var query =  `
      mutation {
        run_job (name:\"${job_name}\"){
          name
          props {key value}
      }
      }
    ` 
    if(props.length > 0) {
      var props_updated = [JSON.stringify(props).replaceAll("\"$","").replaceAll("$\"","")]   
      query =  `
      mutation {
        run_job (name:\"${job_name}\" ,props: ${props_updated}){
          name
          props {key value}
      }
      }
    ` 
    }
    this.setState(prevState => ({
      loader: true
    }))

    const response = await fetchCall(GRAPHQL_URL,query)

    if (!response.ok) {
      var message = `An error has occured: ${response.statusText}`;
      this.setState(prevState => ({
        error_code: response.status
      }))
      throw  new Error(message);
    } 
    else {  
      this.setState(prevState => ({
        loader: false
      }))
      const {data,errors} = await response.json();
      if(JSON.parse(JSON.stringify(data.run_job)) !== null){
        this.setJobErrorDeails(false)
      }
      else {
        this.setJobErrorDeails(true,JSON.parse(JSON.stringify(errors)))
      }
      return data;  
    }  
  }

  setJobErrorDeails(is_error,errors){
    this.setState(prevState => ({
      is_error: is_error,
      errors: errors                    
      }))
  }

  setInterval() {
    this.timerID = setInterval(
      () => this.fetchEtlFlowJobs(),
      1000 * 1000
    );
  }


  showModal(data)
 {
   var props = ''
   var data_jobruns = JSON.parse(data)

   for(var key in data_jobruns) {
   { 
       props  += "<b>"+ key + "</b>" +"=" + data_jobruns[key]+ "<br></br>"
   }
  {$("#jobRunModal .modal-body").html(props)}
  $("#jobRunModal").modal();
 }
  }

  retryJobRun(job_name,data)
  {
   var final_props = []
   var data_jobruns = JSON.parse(data)
   var default_props=["job_send_slack_notification", "job_enable_db_logging", "job_notification_level"];

   var i = 0
   var final_props = []
   var i;
   for(var key in data_jobruns) {
     if(!default_props.includes(key)){
        final_props[i] =  {["$key$"] : key,["$value$"] : data_jobruns[key]}
        i++
     }
   }
   var props = ''


   this.runEtlJob(job_name,final_props).then(({run_job}) => {
    this.setNotification(true)
    var data =  JSON.parse(JSON.stringify(run_job))
    if(data !== null){
        props = JSON.stringify(data["props"]) 
        var job_name = JSON.stringify(data["name"]) 
        this.setJobDeails(job_name,props)
   }});     
  }

  setJobDeails(job_name,props){
  this.setState(prevState => ({
    retry_job_name: job_name,
    run_job_props: props                    
    }))
  }

  setNotification = state => {
    this.setState(prevState => ({
      notification: state
    }))
  }

  showJobRunStateModal(data)
 {
  {$("#jobRunStateModal .modal-body").html(data)}
  $("#jobRunStateModal").modal();
  }

  handleOnClick(job_id){    
  this.props.history.push('/JobRunDetails/' + job_id)
  }

  render(){

    
   var button = ''
   var filter_button_name = '' 
   var filter_button_job_name = '' 
   var filter_start_date = ''  
   var filter_end_date = ''  
   var filter_back_button = ''

   var message = ''
   var job_props = ''
   var errors = ''
   if(this.state.notification){
     job_props = this.state.run_job_props
     if(this.state.is_error) {
         message = 'Error while submitting job!'
         JSON.parse(JSON.stringify(this.state.errors)).map( (data) => {
           errors = data.message
         })
      } else {
         message = ' Job ' +  this.state.retry_job_name.replace(/^"(.*)"$/, '$1') +  ' submitted successfully! '
       }  
   } 

   //Code for adding pagination element
  if (this.props.paginationValue === 0) {
    button = <li class="page-item disabled">
    <input type="button" value="Previous" class="page-link btn btn-secondary btn-sm"/></li>
  } else {
    button =  <li class="page-item">
    <input type="button" value="Previous" class="page-link btn btn-secondary btn-sm" onClick = {() => {
            this.props.decPaginationValue(this.state.paginationValue - 10)
    }}/></li>
  } 

   //Code for adding button name
  if (this.props.filter !== '') {
    filter_button_name = this.props.filter
  }else {
    filter_button_name = "Add Filter"
  } 

   //Code for adding button name
  if (this.props.job_name !== '') {
     filter_button_job_name = this.props.job_name
  }else {
     filter_button_job_name = "Type to search"
  } 

  if (this.props.filter_start_date !== '') {
   filter_start_date = this.props.filter_start_date
  }else {
    filter_start_date = "Start Date"
  } 

  if (this.props.filter_end_date !== '') {
    filter_end_date = this.props.filter_end_date
  }else {
    filter_end_date = "End Date"
  } 

  if(this.props.match.params.job_name !== undefined){
    filter_back_button= <button class= "btn btn btn-sm" type="button " onClick = {() => {
                          this.props.history.push('/')
                         }} >
                        Back
                        </button>
  }

  return (
     <div className="container-fluid">
       <div><Notification notification={this.state.notification} is_error={this.state.is_error}  errors = {this.state.errors}  message={message} job_props = {job_props} setNotification={this.setNotification}/></div>
       <ul class = "pagination justify-content-center">
         <div class="dropdown">
            <button class="btn btn-light dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
               {filter_button_name}
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              { this.filterOperation.map ( filter => (
                   <a class="dropdown-item">
                      <button class= "btn btn btn-sm" type="button " onClick = {() => {
                           this.props.setFilterName(filter)
                       }}> {filter} </button>
                   </a>
              ))}
            </div>
         </div>
         <div className ="form-inline float-right">
             <div className="mb-1">
             <input type="text"
                        className="form-control input-sm"
                        list = "datalistOptions"
                        id = "exampleDataList"
                        placeholder = {filter_button_job_name}
                        onChange={e => this.props.setJobName(document.getElementById("exampleDataList").value)}></input>      
             </div>
             <datalist id = "datalistOptions">
               {this.props.jobs.map( data => (
                 <option value = {data.name}></option>
               ))}
             </datalist> 
         </div>
         <div class = "form-inline float-right">
              <div class = "mb-1"> 
                  <input type="date"
                         className="form-control input-sm"
                         placeholder={filter_start_date}
                         id = "start_date"
                         onChange={e => this.props.setFilterStartDate(document.getElementById("start_date").value)}
                   ></input>
              </div>
              <div class = "mb-1"> 
                  <input type="date"
                         className="form-control input-sm"
                         placeholder={filter_end_date}
                         id = "end_date"
                         onChange={e => this.props.setFilterEndDate(document.getElementById("end_date").value)}
                   ></input> 
              </div>    
         </div> 
         <button class= "btn btn btn-sm" type="button " onClick= {() => {
            this.props.setInitialState()
            }}>
            Clear
         </button>
         <RefreshComponent call = {this.props.setJobRuns}/>
         {filter_back_button}
       </ul>
       <table id="jobruns" class="table table-sm table-bordered">
           <thead class="thead-dark">
                 <tr>
                   <th>Job Name</th>
                   <th>Job Type</th>
                   <th>Job Start Time</th>
                   <th>Elapsed Time</th>
                   <th>Job Status</th>
                   <th>Actions</th>
                 </tr>
               </thead>
                 <tbody>
                 {this.props.data.map( data => (
                   <tr>
                     <td> <button type="button" class="btn btn-link" data-dismiss="modal" onClick = {() => this.handleOnClick(data.job_run_id)}>{data.job_name}</button></td>
                     <td>{data.job_type}</td>
                     <td>{data.start_time}</td>
                     <td>{data.elapsed_time}</td>
                     <td>
                     {(() => {
                           if (data.state === "pass" || data.state == "" ) {
                             return (
                               <div>
                                 <p className = "text-success">{data.state}</p>
                               </div>
                             )
                           } else if (data.state === "started" || data.state == "running" ) {
                             return (
                               <div>
                                 <p className = "text-warning">{data.state}</p>
                               </div>
                             )
                           } else {
                            return (
                              <div>
                                 <button type="button" class="btn btn-danger btn btn-sm" data-toggle="modal" onClick = {() => this.showJobRunStateModal(data.state)}>
                                     Error
                                 </button>
                            
                                 <div class="modal fade" id="jobRunStateModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                   <div class="modal-dialog" role="document">
                                     <div class="modal-content">
                                       <div class="modal-header">
                                         <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                         <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                           <span aria-hidden="true">&times;</span>
                                         </button>
                                       </div>
                                       <div class="modal-body">
                                       </div>
                                       <div class="modal-footer">
                                         <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                       </div>
                                     </div>
                                   </div>
                                 </div>
                              </div>
                            )
                           }
                         })()}
                     </td>
                     <td>
                     <div class='row'>
                     <div class='col-xs-12 button-wrapper' class="inner">
                         <button type="button" class="btn btn-secondary btn-sm ml-2" data-toggle="modal" onClick = {() => this.showModal(data.properties)}>
                           Properties
                         </button>
                         
                         <div class="modal fade" id="jobRunModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                           <div class="modal-dialog" role="document">
                             <div class="modal-content">
                               <div class="modal-header">
                                 <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                 <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                   <span aria-hidden="true">&times;</span>
                                 </button>
                               </div>
                               <div class="modal-body">
                               </div>
                               <div class="modal-footer">
                                 <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                               </div>
                             </div>
                           </div>
                         </div>

                         {(() => {
                           if (data.state === "pass" || data.state == "" || data.state == "started" ) {
                             return (
                               <div>
                               </div>
                             )
                           } else {
                             return (
                               <div class="inner">  
                                  <button type="button" class="btn btn-warning btn-sm ml-2" data-toggle="modal" onClick = {() => this.retryJobRun(data.job_name,data.properties)}>
                                      Retry
                                  </button>
                               </div>
                             )
                           } 
                         })()}
                        </div>
                        </div> 
                     </td>
                   </tr>
                 ))}
                 </tbody>
            </table>  
            <nav aria-label="Page navigation example">
               <ul class="pagination justify-content-center">
                 {button}
                 <li class="page-item"><input type="button" value="Next" class="page-link btn btn-secondary btn-sm" onClick = {() => {
                    this.props.incPaginationValue()
                   }}/>
                 </li>
               </ul>
            </nav>
          </div>
   );
  }
}

// export default JobRuns; 
export default withRouter(JobRunsData)
